import { useRouter } from "next/router";
import Image from "next/image";
import Link from "next/link";

const events = [
  {
    id: 1,
    title: "Los Angeles Lakers vs Miami Heat",
    date: "Jan 18, 2025",
    location: "Crypto Arena, LA",
    img: "/images/sports.jpg",
    category: "Sports",
    description:
      "Experience an intense matchup between the Lakers and the Heat. A night full of action, energy, and thrilling basketball!",
  },
  {
    id: 2,
    title: "Arijit Singh Live Concert",
    date: "Feb 12, 2025",
    location: "Mumbai",
    img: "/images/concerts.jpg",
    category: "Concert",
    description:
      "Arijit Singh performs live with a magical musical night filled with soulful songs and astonishing energy!",
  },
  {
    id: 3,
    title: "Broadway: The Lion King",
    date: "Feb 25, 2025",
    location: "New York Theatre",
    img: "/images/theatre.jpg",
    category: "Theatre",
    description:
      "Watch The Lion King come alive on stage with breathtaking visuals, music, and performances.",
  },
  {
    id: 4,
    title: "Sunburn Festival",
    date: "Dec 27, 2025",
    location: "Goa",
    img: "/images/festival.jpg",
    category: "Festival",
    description:
      "Asia's biggest music festival returns with an insane lineup, lights, and unforgettable vibes.",
  },
  {
    id: 5,
    title: "Chicago Bulls vs Boston Celtics",
    date: "Jan 21, 2025",
    location: "United Center, Chicago",
    img: "/images/sports.jpg",
    category: "Sports",
    description:
      "The Bulls take on the Celtics in a thrilling Eastern Conference showdown!",
  },
];

export default function EventDetails() {
  const router = useRouter();
  const { id } = router.query;

  const event = events.find((e) => e.id === Number(id));

  if (!event) {
    return (
      <div className="text-white text-center py-20 text-xl">
        Event not found.
      </div>
    );
  }

  // ðŸ”¥ FILTER RELATED EVENTS (same category + not same id)
  const related = events
    .filter(
      (e) => e.category === event.category && e.id !== event.id
    )
    .slice(0, 3);

  return (
    <div className="min-h-screen bg-gray-900 text-white px-6 md:px-16 py-16">
      {/* Hero Image */}
      <div className="w-full h-72 md:h-96 relative rounded-xl overflow-hidden shadow-lg mb-10">
        <Image
          src={event.img}
          alt={event.title}
          fill
          className="object-cover"
        />
      </div>

      {/* Event Content */}
      <div className="max-w-3xl mx-auto">
        <span className="text-blue-400 font-semibold text-sm">
          {event.category}
        </span>

        <h1 className="text-4xl font-bold mt-2">{event.title}</h1>

        <p className="text-gray-400 text-lg mt-2">{event.location}</p>
        <p className="text-gray-400 text-lg">{event.date}</p>

        <p className="mt-6 text-gray-300 leading-relaxed">
          {event.description}
        </p>

        {/* CTA Buttons */}
        <div className="flex gap-6 mt-10">
          <button className="px-6 py-3 bg-blue-600 hover:bg-blue-500 rounded-lg text-lg">
            Buy Tickets
          </button>

          <button
            onClick={() => router.push("/events")}
            className="px-6 py-3 border border-gray-600 hover:border-white rounded-lg text-lg"
          >
            Back to Events
          </button>
        </div>
      </div>

      {/* â­ RELATED EVENTS SECTION */}
      {related.length > 0 && (
        <div className="mt-20">
          <h2 className="text-3xl font-bold mb-8">Related Events</h2>

          <div className="grid md:grid-cols-3 gap-10">
            {related.map((e) => (
              <Link key={e.id} href={`/events/${e.id}`}>
                <div className="bg-gray-800 rounded-xl shadow-lg hover:shadow-blue-500/20 hover:scale-[1.02] transition p-4 cursor-pointer">
                  <Image
                    src={e.img}
                    width={500}
                    height={300}
                    alt={e.title}
                    className="rounded-lg mb-4"
                  />

                  <div className="text-sm text-blue-400 font-semibold">
                    {e.category}
                  </div>

                  <h3 className="text-lg font-bold mt-2">{e.title}</h3>
                  <p className="text-gray-400 mt-1">{e.location}</p>
                  <p className="text-gray-400 mt-1">{e.date}</p>
                </div>
              </Link>
            ))}
          </div>
        </div>
      )}
    </div>
  );
}
